.single {
  display: flex;
  gap: 50px;
  margin: 30px auto;
  max-width: 80%;

  .post {
    flex: 3;
    display: flex;
    flex-direction: column;
    gap: 30px;

    img {
      height: 300px;
      object-fit: cover;
    }

    .user {
      display: flex;
      align-items: center;
      gap: 10px;
      font-size: 14px;

      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        object-fit: cover;
      }

      span {
        font-weight: bold;
      }
    }

    h1 {
      font-size: 42px;
      color: rgb(51, 51, 51);
    }

    p {
      text-align: justify;
      line-height: 30px;
    }
  }

  .recommend {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 25px;

    h1 {
      font-size: 20px;
      color: rgb(85, 85, 85);
    }

    .post {
      display: flex;
      flex-direction: column;
      gap: 10px;

      img {
        width: 100%;
        height: 200px;
        object-fit: cover;
      }

      h2 {
        color: rgb(85, 85, 85);
      }

      button {
        width: max-content;
        padding: 7.5px 15px;
        border: none;
        cursor: pointer;
        color: rgb(9, 13, 87);
        background-color: white;
        border: 1px solid rgb(9, 13, 87);

        &:hover {
          border: 1px solid white;
          background-color: rgb(9, 13, 87);
          color: white;
        }
      }
    }
  }

  @media (max-width: 480px) {
    flex-direction: column;
    margin: auto;

    .post {
      img {
        width: 100%;
        height: 200px;
      }

      h1 {
        font-size: 30px;
        color: rgb(51, 51, 51);
      }

      p {
        line-height: 25px;
      }
    }
  }
}
